import orderType from '../../common/public.js'
export default {
	data() {
		return {
			imgUrl: 'https://sinomach.wxzhixun.com/images/', //图片接口
			curHdIndex: '0',
			orderId: '',
			orderList: [],
			type: 1,
			// orderList:[]
		}
	},
	onShow() {
		// sessionStorage.setItem('aaa', '123123order');
		this.curHdIndex = '0';
		orderType.delOrderType = true
		this.getOrderList(1);
	},
	onLoad(e) {
		this.token = sessionStorage.getItem('token') 
	},
	methods: {
		//删除订单
		delItem(theId, index) {
			let that = this;
			uni.showModal({
				title: '提示',
				content: '确定删除该订单?',
				success: function(res) {
					if (res.confirm) {
						uni.request({
							url: that.nowUrl + '/foods/shopcar/remove?id=' + theId,
							header: {
								'token': that.token
							},
							method: 'POST',
							success(res) {
								// console.log(res)
								// uni.navigateTo({
								// 	url: '../index/index'
								// })
								if (res.data.code == 200) {
									// that.getOrderList(1);
									uni.showToast({
										title: '删除成功',
										duration: 1500
									});
									that.orderList.splice(index, 1);
								} else {
									uni.showToast({
										icon: "none",
										title: res.data.msg,
										duration: 1500
									});
								}

								// uni.navigateTo({
								// 	url: '../index/index',
								// 	animationType: 'slide-in-right',
								// 	animationDuration: 200
								// });
							}
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})

		},
		tabFun(index) { //切换tab
			this.curHdIndex = index - 1;
			this.getOrderList(index);
			//请求数据，替换orderList
			//	this.orderList = [];
		},
		waitingMeal(type, orderId) { //跳待取餐
			uni.navigateTo({
				url: '../waitingMeal/waitingMeal?id=' + orderId + '&type=' + this.type,
				animationType: 'slide-in-right',
				animationDuration: 200
			})

		},
		/**
		 * 获取订单列表
		 * type: number -1 取消的 0-待提交的 1-待接收 2-待取餐 3-待评价 4-已评价
		 */
		getOrderList(index) {
			let type = ''
			switch (index) {
				case 1:
					type = 2
					break
				case 2:
					type = 3
					break
				case 3:
					type = 4
					break
				case 4:
					type = 0
					break
			}
			uni.showLoading({
				mask: true
			})
			// console.log(type)
			this.type = type
			uni.request({
				url: this.nowUrl + '/foods/myOrder/query',
				method: 'get',
				data: {
					type: type
				},
				header: {
					"content-type": "application/json",
					"token": this.token
				},
				success: (res) => {
					// console.log(res.data);
					this.orderList = res.data.data;
					uni.hideLoading()
				}
			});
		}
	}
}
